/* 图片预览 */
.vue-uploader .file-list .file-item{ position: relative;float: left;width: 150px;height: 150px;margin: 0 20px 20px 0;text-align: center;
}
.vue-uploader .file-list .file-item img{ width: 100%;height: 100%;border: 1px solid #ececec;
}
.vue-uploader .file-list .file-item .file-remove{font-size: 20px;font-weight: bold;line-height: 20px; position: absolute;top: -10px;right: -10px;width: 20px;height: 20px;-webkit-transform: rotate(45deg);-ms-transform: rotate(45deg);transform: rotate(45deg);text-align: center;color: #f00;border: 1px solid #f00;border-radius: 50%;background: transparent;
}
/* add */
.vue-uploader .add{ font-size: 100px;line-height: 150px;float: left;width: 150px;height: 150px;cursor: pointer;text-align: center;color: #a9a9a9;border: 1px dashed #a9a9a9;
}
/* file */
.vue-uploader > input[type='file']{ display: none;
}
/* progre */
.vue-uploader .imgProgress-bar{ -webkit-box-flex: 1; -ms-flex-positive: 1; flex-grow: 1;
}
.vue-uploader .imgProgress-bar section{font-size: 12px; margin-top: 5px;-webkit-transition: all .5s ease;-o-transition: all .5s ease;transition: all .5s ease;text-align: center;color: #fff;border-radius: 3px;background: #00b4aa;
}
